import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Row,
  Col,
  Label,
} from "reactstrap";
import ReactDatetime from "react-datetime";
import moment from "moment";
import {
  getDate,
  getDateTime,
  getUserFullName,
} from "../../../helpers/UtilsHelper";
import { getVenue } from "../../../redux/actions/bookingAction";
import {
  getListBookingExport,
  clearBookingData,
} from "../../../redux/actions/exportBookingAction";
import { toast } from "react-toastify";
import { saveAs } from "file-saver";
import { useState } from "react";

const { detect } = require("detect-browser");
const browser = detect();
const isValidBrowser = browser.name !== "edge" && browser.name !== "ie";

const today = moment().startOf("minute");
var ics = null;
if (isValidBrowser) {
  ics = require("ics");
}

export default function ExportICal(props) {
  const dispatch = useDispatch();
  const { toggle, isOpen } = props;
  const bookingData = useSelector((state) => state.exportBooking.data);
  const venueData = useSelector((state) => state.booking.venueData);

  const [start_date, setStart_date] = useState("");
  const [end_date, setEnd_date] = useState("");

  const handleReactDatetimeChange = (who, date) => {
    if (
      start_date &&
      who === "end_date" &&
      new Date(start_date._d + "") > new Date(date._d + "")
    ) {
      setStart_date(date);
      setEnd_date(date);
    } else if (
      end_date &&
      who === "start_date" &&
      new Date(end_date._d + "") < new Date(date._d + "")
    ) {
      setStart_date(date);
      setEnd_date(date);
    } else {
      if (who === "start_date") {
        setStart_date(date);
      } else {
        setEnd_date(date);
      }
    }
  };

  const getClassNameReactDatetimeDays = (date) => {
    if (start_date && end_date && start_date._d + "" !== end_date._d + "") {
      if (
        new Date(end_date._d + "") > new Date(date._d + "") &&
        new Date(start_date._d + "") < new Date(date._d + "")
      ) {
        return " middle-date";
      }
      if (end_date._d + "" === date._d + "") {
        return " end-date";
      }
      if (start_date._d + "" === date._d + "") {
        return " start-date";
      }
    }
    return "";
  };

  const getBookingData = () => {
    return new Promise((resolve, reject) => {
      const filters = {
        start_date: start_date ? getDate(start_date._d) : "",
        end_date: end_date ? getDate(end_date._d) : "",
      };
      dispatch(getListBookingExport(filters));
      dispatch(getVenue());
      let timeCount = 0;
      const waitBookingData = setInterval(() => {
        timeCount++;
        if (bookingData && bookingData.result) {
          clearInterval(waitBookingData);
          resolve(bookingData);
        } else if (timeCount > 50) {
          clearInterval(waitBookingData);
          reject("There is no booking");
        }
      }, 100);
    });
  };

  const getLocationName = (space) => {
    if (!space) {
      return "Alternate space";
    } else if (venueData && venueData.result) {
      const venue = venueData.result.find((v) => v.id === space.venue);
      return `${venue.name} | ${space.name}`;
    }
  };

  const exportICal = async () => {
    getBookingData()
      .then((bookingData) => {
        dispatch(clearBookingData());
        if (bookingData && bookingData.result) {
          let events = bookingData.result.map((item) => {
            const status = item.status === 0 ? "CONFIRMED" : "CANCELLED";
            const duration = moment.duration(
              moment(item.endTime, "HH:mm:ss").diff(
                moment(item.startTime, "HH:mm:ss")
              )
            );
            const convertDuration = {
              hours: duration._data.hours,
              minutes: duration._data.minutes,
            };
            const start = [
              moment(item.startDate).year(),
              moment(item.startDate).month() + 1,
              moment(item.startDate).date(),
              moment(item.startTime, "hh:mm:ss").hour(),
              moment(item.startTime, "hh:mm:ss").minute(),
            ];
            const { comment } = item;
            const providerName = getUserFullName(item.provider);
            const clientName = getUserFullName(item.client);
            return {
              title: `Meet ${clientName}`,
              location: getLocationName(item.space),
              duration: convertDuration,
              status: status,
              start: start,
              description: comment,
              organizer: { name: providerName },
              attendees: [{ name: clientName }, { name: providerName }],
            };
          });
          const { error, value } = ics.createEvents(events);
          if (error) {
            console.log(error);
          } else {
            saveFileExport(value);
          }
        } else {
          toast.error("There is no booking in these days!");
        }
      })
      .catch((err) => {
        toast.error(err);
      });
  };

  const saveFileExport = async (value) => {
    const blob = new Blob([value], { type: "text/plain;charset=utf-8" });
    await saveAs(blob, `ICal_${getDateTime(today)}.ics`);
    toggle();
  };

  if (!isValidBrowser) {
    return (
      <Modal className="modal-dialog-centered" isOpen={isOpen} toggle={toggle}>
        <ModalHeader>Warning</ModalHeader>
        <ModalBody>
          Export to Calender function does not support on this browser!
        </ModalBody>
        <ModalFooter>
          <Button
            className="ml-auto"
            color="link"
            data-dismiss="modal"
            type="button"
            onClick={toggle}
          >
            Close
          </Button>
        </ModalFooter>
      </Modal>
    );
  }
  return (
    <Modal className="modal-dialog-centered" isOpen={isOpen} toggle={toggle}>
      <div className="modal-header">
        <h6 className="modal-title" id="modal-title-default">
          Choose date range
        </h6>
        <button
          aria-label="Close"
          className="close"
          data-dismiss="modal"
          type="button"
          onClick={toggle}
        >
          <span aria-hidden={true}></span>
        </button>
      </div>
      <ModalBody>
        <Row>
          <Col>
            <Label className="text-muted">Start Date</Label>
            <ReactDatetime
              inputProps={{
                placeholder: "Choose your start date",
              }}
              value={start_date}
              closeOnSelect={true}
              timeFormat={false}
              onChange={(e) => handleReactDatetimeChange("start_date", e)}
              renderDay={(props, currentDate, selectedDate) => {
                let classes = props.className;
                classes += getClassNameReactDatetimeDays(currentDate);
                return (
                  <td {...props} className={classes}>
                    {currentDate.date()}
                  </td>
                );
              }}
            ></ReactDatetime>
          </Col>
          <Col>
            <Label className="text-muted">End Date</Label>
            <ReactDatetime
              inputProps={{ placeholder: "Choose your end date" }}
              value={end_date}
              closeOnSelect={true}
              timeFormat={false}
              onChange={(e) => handleReactDatetimeChange("end_date", e)}
              renderDay={(props, currentDate, selectedDate) => {
                let classes = props.className;
                classes += getClassNameReactDatetimeDays(currentDate);
                return (
                  <td {...props} className={classes}>
                    {currentDate.date()}
                  </td>
                );
              }}
            ></ReactDatetime>
          </Col>
        </Row>
      </ModalBody>
      <ModalFooter>
        <Button color="info" type="button" onClick={exportICal}>
          Export
        </Button>
        <Button
          className="ml-auto"
          color="link"
          data-dismiss="modal"
          type="button"
          onClick={toggle}
        >
          Close
        </Button>
      </ModalFooter>
    </Modal>
  );
}
