import React, { Component } from "react";
import { connect } from "react-redux";
import {
  Button,
  Card,
  Table,
  Row,
  Col,
  CardHeader,
  CardBody,
  Input,
  Label,
  ButtonGroup,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import ReactDatetime from "react-datetime";
import { ExportToCsv } from "export-to-csv";
import PaginationBar from "../../../components/PaginationBar";
import Loading from "../../../components/Loading";
import {
  getDate,
  getExportDataMyBookingProvider,
} from "../../../helpers/UtilsHelper";
import moment from "moment";
import cons from "../../../constants";
import {
  getList,
  getVenue,
  getSpace,
  setSpace,
  toggleSideBar,
  getSpaceForModal,
  addBooking,
  getClients,
  addAppointment,
  editAppointment,
  deleteAppointment,
} from "../../../redux/actions/bookingAction";
import { getBookingInvoices } from "../../../redux/actions/invoiceAction";
import {
  addNewBooking,
  cancelAddNewBooking,
  getDayBookings,
} from "../../../redux/actions/providerDashboardActions";
import Booking from "./Booking";
import BookingSideBar from "../../../components/BookingSidebar/BookingSideBar";
import "./ListBooking.scss";
import ExportBooking from "./ExportBooking";
import BookingModal from "../Dashboard/BookingModal";

const today = moment().startOf("date");

class ListBooking extends Component {
  constructor(props) {
    super(props);
    this.state = {
      status: "",
      space: "",
      venue: "",
      isLoading: true,
      isOpenSideBar: false,
      dataSideBar: null,
      bookingSelected: "",
      start_date: today.clone(),
      end_date: today.clone().add(1, "M"),
      radio: 0,
      dropdownVenueOpen: false,
      dropdownSpaceOpen: false,
      dropdownVenueName: "Locations",
      dropdownSpaceName: "Spaces",
      selectedVenue: null,
      id: null,
      dropdownStatusOpen: false,
      dropdownStatusName: "Bookings",
    };
    this.filters = {
      limit: 10,
      offset: 0,
      selected: 0,
      ordering: "start_date",
      desc: true,
      status: "",
      space: "",
      start_date: getDate(today.clone()),
      end_date: getDate(today.clone().add(1, "M")),
      venue: "",
    };
  }

  componentDidMount() {
    this.props.getList(this.filters);
    this.props.getVenue();
    this.props.getClients();
  }

  handleChangeFilter = async (e, itemName) => {
    const { value, name } = e.target;
    this.setState({ [name]: value });
    if (name === "venue") {
      if (value === "") {
        this.setState({ space: "", dropdownSpaceName: "All Spaces" });
        this.props.setSpace();
      } else {
        this.props.getSpace(value);
      }
      await this.setState({ dropdownVenueName: itemName });
      this.filter();
    } else {
      await this.setState({ dropdownSpaceName: itemName });
      this.filter();
    }
  };

  filterStatus = (status) => {
    this.setState({ status: status }, () => this.filter());
  };

  filter = () => {
    const { start_date, end_date, space, venue, status } = this.state;
    this.filters = {
      ...this.filters,
      space: space,
      venue: venue,
      status: status,
      start_date: start_date ? getDate(start_date._d) : "",
      end_date: end_date ? getDate(end_date._d) : "",
    };
    this.resetFilterData();
    this.props.getList(this.filters);
  };

  sort = (e) => {
    let name = e.target.attributes.name.value;
    if (name && name === this.filters.ordering) {
      this.filters.desc = !this.filters.desc;
    } else {
      this.filters.ordering = name;
      this.filters.desc = true;
    }
    this.resetFilterData();
    this.props.getList(this.filters);
  };

  selectPageSize = (e) => {
    const { value } = e.target;
    if (value) {
      this.resetFilterData();
      this.setState({ isOpenSideBar: false, tickCheckAll: false });
      this.filters.limit = value;
      this.props.getList(this.filters);
    }
  };

  selectPage = ({ selected }) => {
    this.filters.selected = selected || 0;
    this.filters.offset = Math.ceil(selected * this.filters.limit);
    this.setState({ isOpenSideBar: false, tickCheckAll: false });
    this.props.getList(this.filters);
  };

  handleReactDatetimeChange = async (who, date) => {
    if (
      this.state.start_date &&
      who === "end_date" &&
      new Date(this.state.start_date._d + "") > new Date(date._d + "")
    ) {
      await this.setState({
        start_date: date,
        end_date: date,
      });
      this.filter();
    } else if (
      this.state.end_date &&
      who === "start_date" &&
      new Date(this.state.end_date._d + "") < new Date(date._d + "")
    ) {
      await this.setState({
        start_date: date,
        end_date: date,
      });
      this.filter();
    } else {
      await this.setState({
        [who]: date,
      });
      this.filter();
    }
  };

  getClassNameReactDatetimeDays = (date) => {
    if (this.state.start_date && this.state.end_date) {
    }
    if (
      this.state.start_date &&
      this.state.end_date &&
      this.state.start_date._d + "" !== this.state.end_date._d + ""
    ) {
      if (
        new Date(this.state.end_date._d + "") > new Date(date._d + "") &&
        new Date(this.state.start_date._d + "") < new Date(date._d + "")
      ) {
        return " middle-date";
      }
      if (this.state.end_date._d + "" === date._d + "") {
        return " end-date";
      }
      if (this.state.start_date._d + "" === date._d + "") {
        return " start-date";
      }
    }
    return "";
  };

  resetFilterData = () => {
    this.filters.offset = 0;
    this.filters.selected = 0;
  };

  clearFilterData = () => {
    this.filters = {
      limit: 10,
      offset: 0,
      selected: 0,
      ordering: "start_date",
      desc: false,
      status: "",
      space: "",
      venue: "",
      start_date: "",
      end_date: "",
    };
    this.setState({
      start_date: "",
      end_date: "",
      status: "",
      space: "",
      venue: "",
      dropdownVenueName: "Locations",
      dropdownSpaceName: "Spaces",
      dropdownStatusName: "Bookings",
    });
    this.props.getList(this.filters);
  };

  toggleSideBar = (e, id, state) => {
    const bookingArr = this.props.bookingData.result;
    const isOpenSideBar = state || !this.state.isOpenSideBar;
    let dataSideBar = bookingArr.find((item) => item.id === id);
    this.setState({ dataSideBar });
    this.props.toggleSideBar(isOpenSideBar);
    if (id) {
      this.props.getBookingInvoices(id);
    }
  };

  reloadSidebar = (bookingArr) => {
    bookingArr = bookingArr || this.props.bookingData.result;
    if (bookingArr) {
      const { id } = this.state;
      let dataSideBar = bookingArr.find((item) => item.id === id);
      this.setState({ dataSideBar });
    }
  };

  exportCsv = () => {
    const tableData = this.props.bookingData.result;
    const venueData = this.props.venueData;
    const data = getExportDataMyBookingProvider(tableData, venueData);
    const filename = String(moment().valueOf());
    const options = {
      filename,
      fieldSeparator: ",",
      quoteStrings: '"',
      decimalSeparator: ".",
      showLabels: true,
      showTitle: false,
      useTextFile: false,
      useBom: true,
      useKeysAsHeaders: true,
    };
    const csvExporter = new ExportToCsv(options);
    csvExporter.generateCsv(data);
  };

  toggleExportModal = () => {
    this.setState({ isOpenExportModal: !this.state.isOpenExportModal });
  };

  toggleSelectVenue = () => {
    this.setState({ dropdownVenueOpen: !this.state.dropdownVenueOpen });
  };

  toggleSelectSpace = () => {
    this.setState({ dropdownSpaceOpen: !this.state.dropdownSpaceOpen });
  };

  addNewBooking = () => {
    const startDate = new Date();
    const startMinutes = startDate.getMinutes();
    const nextMinutes = Math.ceil(startMinutes / 15) * 15;
    startDate.setMinutes(nextMinutes);
    startDate.setSeconds(0);
    const endDate = new Date(startDate);
    endDate.setHours(endDate.getHours() + 1);
    this.props.addNewBooking({
      start: startDate,
      end: endDate,
      startStr: startDate.toISOString(),
      endStr: endDate.toISOString(),
    });
  };

  getSpaceForModal = (value) => {
    this.setState({ selectedVenue: value });
    this.props.getSpaceForModal(value);
  };

  bookSpace = (value) => {
    this.props.addBooking(value, this.filters);
  };

  addAppointment = (data, callback) => {
    this.props.addAppointment(data, this.filters, callback);
  };

  editAppointment = (data, callback) => {
    this.props.editAppointment(data, this.filters, callback);
  };

  deleteAppointment = (data, callback) => {
    this.props.deleteAppointment(data, this.filters, callback);
  };

  componentWillReceiveProps(nextProps) {
    if (nextProps.bookingData !== this.props.bookingData) {
      this.reloadSidebar(nextProps.bookingData.result);
    }
  }

  toggleSelectStatus = () => {
    this.setState({ dropdownStatusOpen: !this.state.dropdownStatusOpen });
  };

  handleChangeStatus = (e, itemName, itemId) => {
    this.setState({ dropdownStatusName: itemName });
    this.setState({ status: itemId }, () => this.filter());
  };

  render() {
    const {
      bookingData,
      loading,
      venueData,
      spaceData,
      isOpenSideBar,
      spaceDataForModal,
      cancelAddNewBooking,
      clients,
      dayBookings,
      getDayBookings,
      modalVenue,
    } = this.props;
    const bookingArr = bookingData.result;
    const { limit, selected } = this.filters;
    const {
      venue,
      status,
      dropdownVenueOpen,
      dropdownSpaceOpen,
      dropdownVenueName,
      dropdownSpaceName,
      selectedVenue,
      dropdownStatusOpen,
      dropdownStatusName,
    } = this.state;
    const totalPage = Math.ceil(bookingData.count / limit) || 0;
    const paginationInfo = {
      from: selected * limit + 1,
      to:
        (selected + 1) * limit < bookingData.count
          ? (selected + 1) * limit
          : bookingData.count,
      total: bookingData.count,
      getShowOne: function () {
        return this.from === this.to;
      },
    };
    const paginationSetting = {
      totalPage,
      selected: this.filters.selected,
    };
    const sortClass = {
      title: this.filters.ordering,
      value: this.filters.desc,
      get: function (name) {
        if (this.title === name) {
          return `sort ${this.value ? "sort-desc" : "sort-asc"}`;
        }
        return "sort";
      },
    };

    return (
      <>
        <ExportBooking
          className="modal-dialog-centered"
          isOpen={this.state.isOpenExportModal}
          toggle={this.toggleExportModal}
        />
        <BookingModal
          venues={venueData}
          spaces={spaceDataForModal}
          selectedVenue={selectedVenue}
          getSpaces={(e) => this.getSpaceForModal(e)}
          bookSpace={this.bookSpace}
          cancelAddNewBooking={cancelAddNewBooking}
          clients={clients}
          addAppointment={this.addAppointment}
          deleteAppointment={this.deleteAppointment}
          editAppointment={this.editAppointment}
          dayBookings={dayBookings}
          getDayBookings={getDayBookings}
          modalVenue={modalVenue}
          loading={loading}
        />
        <Col>
          <Card className="mt-3 custom-card">
            <BookingSideBar
              showSidebar={isOpenSideBar}
              callback={this.reloadSidebar}
              toggle={this.toggleSideBar}
              dataSideBar={this.state.dataSideBar}
              venueData={venueData}
              isAdmin={false}
            />
            {loading && <Loading />}
            <CardHeader className="bg-transparent border-0">
              <Row className="d-flex justify-content-between">
                <div className="order-1 d-flex flex-wrap flex-md-nowrap align-items-center mb-2 mb-lg-0 mr-md-0 ml-md-0">
                  <ReactDatetime
                    className="header__custom-date-input"
                    inputProps={{
                      placeholder: "-- -- --",
                    }}
                    closeOnSelect={true}
                    closeOnSelect={true}
                    value={this.state.start_date}
                    timeFormat={false}
                    dateFormat={"MM/DD/YYYY"}
                    onChange={(e) =>
                      this.handleReactDatetimeChange("start_date", e)
                    }
                    renderDay={(props, currentDate, selectedDate) => {
                      let classes = props.className;
                      classes +=
                        this.getClassNameReactDatetimeDays(currentDate);
                      return (
                        <td {...props} className={classes}>
                          {currentDate.date()}
                        </td>
                      );
                    }}
                  ></ReactDatetime>
                  <span>-</span>
                  <ReactDatetime
                    className="header__custom-date-input"
                    inputProps={{
                      placeholder: "-- -- --",
                    }}
                    closeOnSelect={true}
                    closeOnSelect={true}
                    value={this.state.end_date}
                    timeFormat={false}
                    dateFormat={"MM/DD/YYYY"}
                    onChange={(e) =>
                      this.handleReactDatetimeChange("end_date", e)
                    }
                    renderDay={(props, currentDate, selectedDate) => {
                      let classes = props.className;
                      classes +=
                        this.getClassNameReactDatetimeDays(currentDate);
                      return (
                        <td {...props} className={classes}>
                          {currentDate.date()}
                        </td>
                      );
                    }}
                  ></ReactDatetime>
                  <i className="fas fa-calendar-alt"></i>
                </div>
                {/* <ButtonGroup className="order-3 order-md-2 header__status mr-md-0 ml-md-0" data-toggle="buttons">
                  {
                    cons.BOOKING_STATUS_INFO.map((item, i) => {
                      return (
                        <Button
                          key={i}
                          color="default"
                          onClick={() => this.filterStatus(item.id)}
                          className={`text-center pb-3 header__status__item  ${status === item.id ? 'header__status__item--active' : ''}`}>
                          {item.status}
                        </Button>
                      )
                    })
                  }
                </ButtonGroup> */}
                <div className="order-2 order-md-3 d-flex flex-wrap align-items-center mb-2 mb-md-0 mr-auto ml-auto mr-xl-0 ml-xl-0">
                  <Dropdown
                    isOpen={dropdownStatusOpen}
                    toggle={this.toggleSelectStatus}
                    className="mb-2 mb-md-0 header__dropdown order-md-2 order-1"
                  >
                    <DropdownToggle caret>
                      <i className="fas fa-book mr-2" />

                      {dropdownStatusName}
                    </DropdownToggle>
                    <DropdownMenu>
                      {cons.BOOKING_STATUS_INFO &&
                        cons.BOOKING_STATUS_INFO.map((item, i) => {
                          return (
                            <DropdownItem
                              key={i}
                              name="venue"
                              onClick={(e) =>
                                this.handleChangeStatus(
                                  e,
                                  `${item.status}`,
                                  `${item.id}`
                                )
                              }
                              // onClick={() => this.filterStatus(item.id)}
                              value={item.id}
                            >
                              {item.status}
                            </DropdownItem>
                          );
                        })}
                    </DropdownMenu>
                  </Dropdown>
                </div>
                <div className="order-2 order-md-3 d-flex flex-wrap align-items-center mb-2 mb-md-0 mr-auto ml-auto mr-xl-0 ml-xl-0">
                  <Dropdown
                    isOpen={dropdownVenueOpen}
                    toggle={this.toggleSelectVenue}
                    className="mb-2 mb-md-0 header__dropdown"
                  >
                    <DropdownToggle caret>
                      <i className="fas fa-map-marker-alt mr-2" />
                      {dropdownVenueName}
                    </DropdownToggle>
                    <DropdownMenu>
                      <DropdownItem
                        value=""
                        name="venue"
                        onClick={(e) =>
                          this.handleChangeFilter(e, "All Locations")
                        }
                      >
                        All Locations
                      </DropdownItem>
                      {venueData &&
                        venueData.map((item, i) => {
                          return (
                            <DropdownItem
                              key={i}
                              name="venue"
                              onClick={(e) =>
                                this.handleChangeFilter(e, `${item.name}`)
                              }
                              value={item.id}
                            >
                              {item.name}
                            </DropdownItem>
                          );
                        })}
                    </DropdownMenu>
                  </Dropdown>
                  <Dropdown
                    isOpen={dropdownSpaceOpen}
                    toggle={this.toggleSelectSpace}
                    className="header__dropdown"
                  >
                    <DropdownToggle caret>
                      <i className="ni ni-building mr-2"></i>
                      {dropdownSpaceName}
                    </DropdownToggle>
                    <DropdownMenu>
                      <DropdownItem
                        value=""
                        name="space"
                        onClick={(e) =>
                          this.handleChangeFilter(e, "All Spaces")
                        }
                      >
                        All
                      </DropdownItem>
                      {venue &&
                        spaceData &&
                        spaceData.map((item, i) => {
                          return (
                            <DropdownItem
                              key={i}
                              name="space"
                              onClick={(e) =>
                                this.handleChangeFilter(e, `${item.name}`)
                              }
                              value={item.id}
                            >
                              {item.name}
                            </DropdownItem>
                          );
                        })}
                    </DropdownMenu>
                  </Dropdown>
                  <ButtonGroup
                    className="btn-group-toggle"
                    data-toggle="buttons"
                  >
                    {/* <Button
                      className="mb-2 mb-md-0 header__btn-filter"
                      color="default"
                      outline
                      onClick={this.filter}
                    >
                      <span className="header__btn-filter__content">
                        Filter
                      </span>
                      <i className="fas fa-filter fa-xs header__btn-filter__content"></i>
                    </Button> */}
                    <Button
                      className="mb-2 mb-md-0 header__btn-filter"
                      color="default"
                      outline
                      onClick={this.clearFilterData}
                    >
                      <span className="header__btn-filter__content">
                        Clear Filters
                      </span>
                      <i className="fas fa-eraser header__btn-filter__content"></i>
                    </Button>
                  </ButtonGroup>
                </div>
              </Row>
            </CardHeader>
          </Card>
          <div className="d-flex justify-content-between">
            <Button size="sm" onClick={this.addNewBooking} color="success">
              Create Booking
            </Button>
            <div>
              <Button
                color="primary"
                onClick={this.exportCsv}
                size="sm"
                disabled={bookingArr && bookingArr.length === 0}
              >
                <span className="btn-inner--icon mr-1">
                  <i className="fas fa-file-excel" />
                </span>
                <span className="btn-inner--text">Export CSV</span>
              </Button>
              <Button
                color="primary"
                onClick={this.toggleExportModal}
                size="sm"
              >
                <span className="btn-inner--icon mr-1">
                  <i className="fas fa-download" />
                </span>
                <span className="btn-inner--text">Export Cal</span>
              </Button>
            </div>
          </div>

          <Card className="mt-3">
            <CardBody>
              <Table className="align-items-start table-sort" responsive>
                <thead>
                  <tr>
                    <th
                      className={` ${sortClass.get("space__name")}`}
                      name="space__name"
                      onClick={this.sort}
                    >
                      Location
                    </th>
                    <th
                      className={` ${sortClass.get("space__room")}`}
                      name="space__room"
                    >
                      Space
                    </th>

                    <th
                      className={`${sortClass.get("start_date,start_time")}`}
                      name="start_date,start_time"
                      onClick={this.sort}
                    >
                      Date
                    </th>
                    <th> Booking Start </th>
                    <th> Booking End </th>
                    <th
                      className={`${sortClass.get("duration")}`}
                      name="duration"
                      onClick={this.sort}
                    >
                      Duration
                    </th>
                    <th
                      className={`${sortClass.get("status")}`}
                      name="status"
                      onClick={this.sort}
                    >
                      Status
                    </th>
                    <th> Client Name </th>
                    <th> Appointment Start </th>
                    <th> Appointment End </th>
                  </tr>
                </thead>
                <tbody>
                  {bookingArr &&
                    bookingArr.map((item, i) => {
                      return (
                        <Booking
                          key={i}
                          selectedItemId={
                            this.state.dataSideBar && this.state.dataSideBar.id
                          }
                          check={this.handleCheckbox}
                          openSideBar={this.toggleSideBar}
                          item={item}
                          setStatus={this.getProviderUpdateStatus}
                          venueData={venueData}
                        />
                      );
                    })}
                </tbody>
              </Table>
            </CardBody>
            {bookingArr && bookingArr.length > 0 && (
              <Row className="d-flex justify-content-md-between justify-content-sm-start mb-3 mx-0">
                <Col className="d-flex justify-content-center">
                  {totalPage > 1 && (
                    <PaginationBar
                      setting={paginationSetting}
                      handleClick={this.selectPage}
                    />
                  )}
                </Col>
                <Col
                  className="d-flex align-items-baseline mb-md-0 mb-2"
                  xs="auto"
                >
                  <Input
                    onChange={this.selectPageSize}
                    value={limit}
                    bsSize="md"
                    type="select"
                    name="selectPageSize"
                    id="selectPageSize"
                  >
                    <option value="5">5</option>
                    <option value="10">10</option>
                    <option value="20">20</option>
                    <option value="50">50</option>
                  </Input>
                  <Label className="ml-1 text-nowrap">
                    {/* <small>
                      {`Showing ${paginationInfo.from} ${
                        paginationInfo.getShowOne()
                          ? ""
                          : `to ${paginationInfo.to}`
                      } of ${paginationInfo.total}`}
                    </small> */}

                    <small>
                      Page {`${selected + 1}`} of {`${totalPage}`}
                    </small>
                  </Label>
                </Col>
              </Row>
            )}
          </Card>
        </Col>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    bookingData: state.booking.bookingData,
    venueData: state.booking.venueData,
    spaceData: state.booking.spaceData,
    loading: state.booking.loadBookingData,
    isOpenSideBar: state.booking.isOpenSideBar,
    spaceDataForModal: state.booking.spaceDataForModal,
    modalVenue: state.booking.modalVenue,
    clients: state.booking.clients,
    dayBookings: state.providerDashboard.dayBookings,
  };
};

const mapActionToProps = {
  getList,
  getVenue,
  getSpace,
  setSpace,
  toggleSideBar,
  addNewBooking,
  getSpaceForModal,
  addBooking,
  cancelAddNewBooking,
  getClients,
  addAppointment,
  editAppointment,
  deleteAppointment,
  getBookingInvoices,
  getDayBookings,
};

export default connect(mapStateToProps, mapActionToProps)(ListBooking);
