import React from "react";
import * as moment from "moment";
import { Badge } from "reactstrap";
import _ from "lodash";
import cons from "../../../constants";
import {
  getUserFullName,
  getDateFormat,
  getTime,
  getDurationTime,
  getStatusInfo,
} from "helpers/UtilsHelper";
import default_image from "../../../assets/img/theme/default-image.png";

export default function Booking(props) {
  const { item, selectedItemId, openSideBar, venueData } = props;
  const status = getStatusInfo(cons.BOOKING_STATUS_INFO, item.status);
  const duration = getDurationTime(
    moment(item.startTime, "HH:mm:ss"),
    moment(item.endTime, "HH:mm:ss")
  );
  const unit = duration > 1 ? "hours" : "hour";

  const getSpaceName = (space) => {
    if (!space || (venueData && venueData.length === 0)) {
      return "Alternate space";
    } else {
      const venue = venueData && venueData.find((v) => v.id === space.venue);
      return `${venue.name} `;
    }
  };
  const getSpaceRoom = (space) => {
    if (!space || (venueData && venueData.length === 0)) {
      return "Alternate Room";
    } else {
      return ` ${space.name}`;
    }
  };

  const getSpaceImage = (space) => {
    return _.get(space, "images[0].file", default_image);
  };

  return (
    <tr
      className={
        selectedItemId && item.id === selectedItemId ? "booking__selected" : ""
      }
    >
      <td
        className="cursor-pointer"
        onClick={(e) => openSideBar(e, item.id, true)}
      >
        {getSpaceName(item.space)}
      </td>
      <td
        className="cursor-pointer"
        onClick={(e) => openSideBar(e, item.id, true)}
      >
        {getSpaceRoom(item.space)}
      </td>

      <td>{getDateFormat(item.startDate)}</td>
      <td>{getTime(`${item.startDate} ${item.startTime}`)}</td>
      <td>{getTime(`${item.startDate} ${item.endTime}`)}</td>
      <td>
        {duration} {unit}
      </td>
      <td>
        <Badge color={status.color} pill className="cursor-pointer">
          <b>{status.status}</b>
        </Badge>
      </td>
      <td>
        {item.appointments && item.appointments.length > 0
          ? item.appointments.map((elm, i) => {
              return <div key={i}>{getUserFullName(elm.client)}</div>;
            })
          : ""}
      </td>
      <td>
        {item.appointments && item.appointments.length > 0
          ? item.appointments.map((elm, i) => {
              return (
                <div key={i}>
                  {getTime(`${item.startDate} ${elm.startTime}`)}
                </div>
              );
            })
          : ""}
      </td>
      <td>
        {item.appointments && item.appointments.length > 0
          ? item.appointments.map((elm, i) => {
              return (
                <div key={i}>{getTime(`${item.startDate} ${elm.endTime}`)}</div>
              );
            })
          : ""}
      </td>
    </tr>
  );
}
